#home {
	flex-direction: column;
	.login-informaction {
	    display: flex;
	    width: 100%;
	    justify-content: space-between;
	    height: 2rem; }
	.menuButList {
		flex-grow: 1;
		width: 100%;
		overflow: auto; }
	.but-column {
		display: flex;
		width: 100%;
		justify-content: center; }
	.main-but {
		position: relative;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: rgb(14, 65, 121);
		border-radius: 0.6rem; }
	.but-warp {
		margin: 2rem 2rem 1rem 2rem;
		width: 11rem;
		height: 9rem;
		overflow: hidden; }

	.main-but i {
		display: block;
		width: 100%;
		width: 4.4rem;
		margin: 0.8rem; }
	.but-color {
		background-color: #bfbfbf; }
	.homeMenu-font-style {
		position: absolute;
		bottom: 1rem;
		right: 1rem;
		color: #fff;
		font-size: 1.8rem; }
	@media screen and (max-width: 737px) {
		.but-warp {
			margin: 1rem 1rem 0rem 1rem;
			float: left;
			width: 11rem;
			height: 8rem;
			overflow: hidden; } } }



